import {Col, Row, Spacer, Text} from "@nextui-org/react";
import ArrowLeft from "../../../../images/arrowLeft.svg"
import moment from "moment";

export default function PosterCalendarControls({currentMonth, currentYear, onNextClick, onPrevClick}) {

   return (
       <Row align={"center"} justify={"space-between"} css={{maxWidth: "350px", h: "70px"}}>
                <Col css={{w: "max-content", cursor: "pointer"}} onClick={onPrevClick}>
                    <img style={{
                        filter: !(currentYear === moment().year() && currentMonth === moment().month()) ? "invert(100%)" : "invert(30%)",
                        width: "50px", position: "relative"}} src={ArrowLeft}/>
                </Col>
                <Spacer x={2}/>
                <Text css={{textTransform: "capitalize", fontSize: "48px", position: "relative", top: "-5px"}}>{moment().month(currentMonth).format("MMMM")}</Text>
                <Spacer x={2}/>
                <Col css={{w: "max-content", cursor: "pointer"}} onClick={onNextClick}>
                    <img style={{filter: "invert(100%)", transform: "rotate(180deg)", width: "50px"}} src={ArrowLeft}/>
                </Col>
       </Row>
   )
}