import {Row, Text} from "@nextui-org/react";
import {Children} from "react";

export default function BlockTitle({children}) {
    return <Row justify={"center"} css={{h: "15%"}} align={"center"}><Text css={{fontFamily: "DIN Condensed", fontSize: "96px"}}>{Children.map(children, child => (child))}</Text></Row>
}