import {Row, Grid, Text} from "@nextui-org/react";
import moment from "moment";

const weekDays = () => {
    const weekDays = [];
    for (let idx = 0; idx < 7; idx++) {
        weekDays.push(idx)
    }
    return weekDays
}

export default function PosterCalendarWeeks() {
   return (
       <Row css={{borderTop: "1px solid", borderBottom: "1px solid", h: "70px"}}>
           <Grid.Container>
               {weekDays().map(weekDay => (
                   <Grid justify={"center"} xs={12/7}><Text css={{textTransform: "uppercase", fontFamily: "DIN Condensed", fontSize: "40px"}}>{moment().weekday(weekDay).format("dd")}</Text></Grid>
               ))}
           </Grid.Container>
       </Row>
   )
}