import {Row, Grid, Text} from "@nextui-org/react";
import moment from "moment";

export default function PosterCalendarDays({height, currentMonth, currentYear, currentDay, onSelectDay}) {

   const dayCells = () => {
        let days = [];
        for (let idx = 0; idx < moment().month(currentMonth.value).year(currentYear.value).daysInMonth(); idx++) {
            const day = idx + 1;
            days.push((
                <Grid xs={12/7} justify={"center"} alignItems={"center"} css={{cursor: "pointer"}}
                      onClick={() => onSelectDay(day)}
                >
                    <Row css={{height: "96px"}} justify={"center"} align={"center"}>
                        <Text css={{
                            transition: "200ms",
                            fontFamily: "DIN Condensed",
                            lineHeight: "100%",
                            color: day === currentDay.value ? "#8F2512" : "black",
                            fontSize: day === currentDay.value ? "96px" : "40px"}}
                        >{day}</Text>
                    </Row>
                </Grid>
            ))
        }
        const firstDayOfWeek = moment().date(1).month(currentMonth.value).year(currentYear.value).weekday();
        const append = [];
        for (let idx = 0; idx < firstDayOfWeek; idx++) {
            append.push(<Grid xs={12/7}></Grid>)
        }

        days = [...append, ...days]
            return days;
    }

   return (
       <Row css={{h: `${height}px`}}>
           <Grid.Container css={{h: "100%"}}>
               {dayCells().map(day => (day))}
           </Grid.Container>
       </Row>
   )
}