import {Col, Container, Grid, Row, Spacer, Text} from "@nextui-org/react";
import moment from "moment";
import "moment/locale/ru"
import {useEffect, useRef, useState} from "react";
import useWindowSize from "../../../../hooks/useWindowSize";
import PosterCalendarControls from "./PosterCalendarControls";
import useValue from "../../../../hooks/useValue";
import PosterCalendarWeeks from "./PosterCalendarWeeks";
import PosterCalendarDays from "./PosterCalendarDays";


export default function PosterCalendar() {
    moment.locale("ru")

    const currentDay = useValue(moment().date());
    const currentMonth = useValue(moment().month());
    const currentYear = useValue(moment().year());

    const onPrevClick = () => {
        if (currentYear.value === moment().year() && currentMonth.value === moment().month()) {
            return
        }

        let prevMonth = currentMonth.value - 1
        if (prevMonth === -1) {
            prevMonth = 11
            currentYear.set(currentYear.value - 1)
        }
        currentMonth.set(prevMonth)
    }

    const onNextClick = () => {
        let nextMonth = currentMonth.value + 1
        if (nextMonth === 12) {
            nextMonth = 0
            currentYear.set(currentYear.value + 1)
        }
        currentMonth.set(nextMonth)
    }

    const onSelectDay = (day) => {
        currentDay.set(day);
    }

    const ref = useRef();
    const containerHeight = useValue(1080);
    const windowSize = useWindowSize();
    useEffect(() => {
        if (!ref.current) {
            return
        }
        containerHeight.set(ref.current.offsetHeight);
    }, [ref.current, windowSize.height])

    return (
        <div ref={ref} style={{height: "100%", width: "100%"}}>
        <Container css={{h: "100%", w: "100%"}}>
            <PosterCalendarControls
                currentMonth={currentMonth.value}
                currentYear={currentYear.value}
                onNextClick={onNextClick}
                onPrevClick={onPrevClick}/>

            <PosterCalendarWeeks/>

            <PosterCalendarDays
                height={containerHeight.value - 70 - 70}
                currentDay={currentDay}
                currentMonth={currentMonth}
                currentYear={currentYear}
                onSelectDay={onSelectDay}
            />
        </Container>
        </div>

    )
}